import { Component, OnInit } from "@angular/core";
import { RemoteShopService } from "src/app/core/services/remote-shop.service";
import { Constants } from "src/app/_helpers/constant";
import * as moment from "moment";
import { ToastNotificationService } from "src/app/core/services/toast-notification.service";
import { TitleCasePipe } from "@angular/common";

@Component({
  selector: "app-remote-order",
  templateUrl: "./remote-order.component.html",
  styleUrls: ["./remote-order.component.scss"],
})
export class RemoteOrderComponent implements OnInit {
  public orderData: any;
  public orderData1: any;

  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public searchKey: any;
  public totalbillAmount: any;
  public invoicedate: any;
  public dateRange: Date[];
  public today: Date;
  public selectedDate: any;
  public todaysDate;
  public orderlist: any;

  constructor(
    private remoteShopService: RemoteShopService,
    private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe
  ) {
    this.getClientPendingOrder();
  }

  ngOnInit(): void {
    this.todaysDate = new Date(new Date().setDate(new Date().getDate()));
    this.getClientPendingOrder();
  }

  getClientPendingOrder() {
    this.remoteShopService
      .getGeneratedOrder({}, Constants.currentUser.id, 0)
      .then((res) => {
        console.log(res);
        this.orderData = res.data;
        this.getClientPendingOrder();
      });
  }

  getAllclientPendingOrderSearch1() {
    this.remoteShopService
      .getRemoteOrder1(
        {},
        Constants.currentUser.id,
        0,
        moment(this.todaysDate, "DD-MMM-YYYY").format("YYYY-MM-DD")
      )
      .then((res) => {
        console.log(res);
        this.orderData1 = res.data;
        if (this.orderData1.length > 0) {
          this.orderData = res.data;
        } else {
          this.getClientPendingOrder();
        }
      });
  }

  getAllRemoteOrderSearch(searchkey) {
    console.log(this.searchKey);
    if (this.searchKey == "") {
      this.toastService.openErrorSnackBar("Please enter Party name!!!");
      this.remoteShopService
        .getRemoteOrdersearch({}, this.currentUser.id, 0, searchkey)
        .then((res) => {
          console.log(res);
          this.orderData = res.data;
        });
    } else {
      this.remoteShopService
        .getRemoteOrdersearch({}, this.currentUser.id, 0, searchkey)
        .then(
          (res) => {
            console.log(res);
            this.orderData1 = res.data;
            if (this.orderData1.length > 0) {
              this.orderData = res.data;
              this.toastService.openSnackBar("Record Found Successfully!!!");
            } else {
              this.toastService.openErrorSnackBar("Record Not Found!!!");
            }
          },
          (err) => {
            if (err.error.expose) {
              this.toastService.openErrorSnackBar(
                this.titleCasePipe.transform(err.error.error_message)
              );
            } else {
              this.toastService.openErrorSnackBar("Something Went Wrong.");
            }
          }
        );
    }
  }


  changeDate(e){
    this.getAllclientPendingOrderSearch1();
  }
}
