import { NgModule, Component } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RemoteOrderComponent } from './remote-order/remote-order.component';
import { GenerateReceiptComponent } from './generate-receipt/generate-receipt.component';
import { RemoteShoppingComponent } from './remote-shopping/remote-shopping.component';
import { CommonModule } from '@angular/common';

const routes: Routes = [
  {
    path:'',
    component: RemoteShoppingComponent,
    children: [
      {
        path:'',
        redirectTo: 'remote-order',
        pathMatch: 'full'
      },
      {
        path:'remote-order',
        component:RemoteOrderComponent,
      },
      {
        path:'generate-receipt',
        component:GenerateReceiptComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes), CommonModule],
  exports: [RouterModule]
})
export class NewRemoteShoppingRoutingModule { }
